import { Box, HStack, Menu, Portal, Text } from '@chakra-ui/react';
import Link from 'next/link';
import React from 'react';
import { FaAngleDown } from 'react-icons/fa6';

function DropDown({
  title = 'Book',
  routes = [],
}: {
  title: string;
  routes: Array<{ name: string; path: string }>;
}) {
  return (
    <Menu.Root lazyMount>
      <Menu.Trigger _expanded={{ color: 'primary' }}>
        <HStack cursor={'pointer'}>
          <Text>{title}</Text>
          <Box
            as='span'
            display='inline-flex'
            css={{
              '& > svg': {
                transition: 'transform 0.2s ease',
              },
              '[aria-expanded=true] & > svg': {
                transform: 'rotate(-180deg)',
              },
            }}
          >
            <FaAngleDown />
          </Box>
        </HStack>
      </Menu.Trigger>
      <Portal>
        <Menu.Positioner>
          <Menu.Content gap={0} rounded={20}>
            {routes.map((route) => (
              <Menu.Item
                _hover={{ color: 'red', bg: 'white' }}
                key={route.path}
                bg={'white'}
                value={route.path}
              >
                <Link href={route.path}>{route.name}</Link>
              </Menu.Item>
            ))}
          </Menu.Content>
        </Menu.Positioner>
      </Portal>
    </Menu.Root>
  );
}

export default DropDown;
